<template>
  <div class="setting">
    <div v-if="$route.name == '配置公号'">
      <h2 class="title">
        <slot></slot>
      </h2>
      <Button style="float: left;" class="create" type="primary"
              :to="'/manage/gonghao/tuoguan/setting/create?code=' + $route.query.code">配置新规则
      </Button>
      <Button style="float: right;" type="error" to="/manage/gonghao/tuoguan">关闭</Button>
      <Table class="setting-table" stripe :columns="settingHeader" :data="settingData"></Table>
    </div>
    <router-view/>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        settingData: [],
        settingHeader: [
          {
            title: "类型",
            key: "type",
            align: "center",
            render: (h, params) => {
              let type =
                params.row.type === 0
                  ? "文本"
                  : params.row.type === 1
                  ? "点击"
                  : params.row.type === 2
                    ? "关注"
                    : "默认回复";
              return h(
                "span",
                {
                  props: {}
                },
                type
              );
            }
          },
          {
            title: "性别",
            align: "center",
            render: (h, params) => {
              let sex = params.row.sex;
              sex = sex === 0
                ? '未知' : (sex === 1
                  ? '男' : ( sex === 2
                    ? '女' : '全部'));
              return h('span', {
                props: {}
              }, sex)
            }
          },
          {
            title: "用户回复文本",
            key: "text",
            align: "center"
          },
          {
            title: "点击关键词",
            key: "key",
            align: "center"
          },
          {
            title: "消息msgId",
            key: "msgId",
            align: "center"
          },
          {
            title: "图片",
            key: "showUrl",
            align: "center",
            render: (h, params) => {
              return h("img", {
                attrs: {
                  src: params.row.showUrl,
                  style: "width:100%;padding:10px;"
                }
              });
            }
          },
          {
            title: "Action",
            align: "center",
            render: (h, params) => {
              let that = this;
              return h("div", [
                h(
                  "Button",
                  {
                    style: {
                      marginRight: "10px"
                    },
                    props: {
                      type: "primary",
                      size: "small"
                    },
                    on: {
                      click() {
                        that.$router.push({
                          name: "修改配置",
                          query: {
                            data: params.row,
                            code: that.$route.query.code
                          }
                        });
                      }
                    }
                  },
                  "编辑"
                ),
                h(
                  "Button",
                  {
                    props: {
                      type: "error",
                      size: "small"
                    },
                    on: {
                      click() {
                        that.$Modal.confirm({
                          title: "提示:",
                          content: "确认删除这条内容吗？",
                          onOk: () => {
                            that.$axios
                              .get("/reply/del", {
                                params: {id: params.row._id}
                              })
                              .then(res => {
                                that.settingData.splice(params.index, 1);
                                that.$Message.info("删除成功");
                              });
                          }
                        });
                      }
                    }
                  },
                  "删除"
                )
              ]);
            }
          }
        ]
      };
    },
    mounted() {
      this.showSettingData();
    },
    methods: {
      showSettingData() {
        this.$axios
          .get("/reply", {params: {code: this.$route.query.code}})
          .then(res => {
            this.settingData = res.data.data;
          });
      }
    },
    watch: {
      $route(to, from) {
        if (from.name == "配置新规则" || from.name == "修改配置") {
          this.showSettingData();
        }
      }
    }
  };
</script>

<style scoped>
  .setting {
    float: left;
    width: 100%;
  }

  .form-group {
    width: 500px;
  }

  .title,
  .create {
    margin-bottom: 30px;
  }

  .setting-table {
    clear: both;
  }
</style>
